// -*- ecoding : utf-8 -*-

/**
 * 甘特图控件
 *
 * require : x.js
 */
import x from 'x-lib';

const util = {
  getProgressView: function(progress) {
    return "<div class='cw-gantt-progress' style='width:" + progress + "%; overflow:hidden'></div>";
  },

  // GET NUMBER OF DAYS IN MONTH
  getDaysInMonth: function(month, year) {
    var days;
    switch (month) {
      case 1:
      case 3:
      case 5:
      case 7:
      case 8:
      case 10:
      case 12:
        days = 31;
        break;
      case 4:
      case 6:
      case 9:
      case 11:
        days = 30;
        break;
      case 2:
        if ((year % 4 == 0 && year % 100 != 0) || year % 400 == 0) days = 29;
        else days = 28;
        break;
    }
    return days;
  }
};

const gantt = {
  /**
   * 甘特图对象
   */
  newGantt: function(element) {
    var gantt = {
      // 甘特视图
      ganttView: element,

      ganttText: '甘特视图',

      taskColumnWidthValue: 200,

      // 任务列表
      tasks: [],

      addTask: function(value) {
        this.tasks.push(value);
      },

      draw: function() {
        console.log(this.ganttView);

        // Let 140 | Top 240
        var offsetX = this.ganttView.offsetLeft + 5;
        var offsetY = this.ganttView.offsetTop + 20;

        var _offSet = 0;
        var _dateDiff = 0;
        var _currentDate = new Date();
        var _maxDate = new Date();
        var _minDate = new Date();
        var _dTemp = new Date();

        var _firstRowStr = '<table class="cw-gantt" ><tr><td rowspan="2" style="width:' + this.taskColumnWidthValue + 'px;" ><div class="cw-gantt-task-title" >' + this.ganttText + '</div></td>';

        var _thirdRow = '';
        var _gStr = '';
        var _colSpan = 0;
        var counter = 0;

        _currentDate.setFullYear(_currentDate.getFullYear(), _currentDate.getMonth(), _currentDate.getDate());

        if (this.tasks.length > 0) {
          _maxDate.setFullYear(this.tasks[0].getTo().getFullYear(), this.tasks[0].getTo().getMonth(), this.tasks[0].getTo().getDate());
          _minDate.setFullYear(this.tasks[0].getFrom().getFullYear(), this.tasks[0].getFrom().getMonth(), this.tasks[0].getFrom().getDate());
          for (let i = 0; i < this.tasks.length; i++) {
            if (Date.parse(this.tasks[i].getFrom()) < Date.parse(_minDate)) _minDate.setFullYear(this.tasks[i].getFrom().getFullYear(), this.tasks[i].getFrom().getMonth(), this.tasks[i].getFrom().getDate());
            if (Date.parse(this.tasks[i].getTo()) > Date.parse(_maxDate)) _maxDate.setFullYear(this.tasks[i].getTo().getFullYear(), this.tasks[i].getTo().getMonth(), this.tasks[i].getTo().getDate());
          }

          //---- Fix _maxDate value for better displaying-----
          // Add at least 5 days

          if (_maxDate.getMonth() == 11) {
            //December
            if (_maxDate.getDay() + 5 > util.getDaysInMonth(_maxDate.getMonth() + 1, _maxDate.getFullYear())) {
              _maxDate.setFullYear(_maxDate.getFullYear() + 1, 1, 5); //The fifth day of next month will be used
            } else _maxDate.setFullYear(_maxDate.getFullYear(), _maxDate.getMonth(), _maxDate.getDate() + 5); //The fifth day of next month will be used
          } else {
            if (_maxDate.getDay() + 5 > util.getDaysInMonth(_maxDate.getMonth() + 1, _maxDate.getFullYear())) {
              _maxDate.setFullYear(_maxDate.getFullYear(), _maxDate.getMonth() + 1, 5); //The fifth day of next month will be used
            } else _maxDate.setFullYear(_maxDate.getFullYear(), _maxDate.getMonth(), _maxDate.getDate() + 5); //The fifth day of next month will be used
          }

          //--------------------------------------------------

          _gStr = '';
          _gStr += '</tr><tr>';
          _thirdRow = '<tr><td>&nbsp;</td>';
          _dTemp.setFullYear(_minDate.getFullYear(), _minDate.getMonth(), _minDate.getDate());
          while (Date.parse(_dTemp) <= Date.parse(_maxDate)) {
            if (_dTemp.getDay() % 6 == 0) {
              //Weekend
              _gStr += "<td class='cw-gantt-weekend'><div style='width:24px;'>" + _dTemp.getDate() + '</div></td>';
              if (Date.parse(_dTemp) == Date.parse(_currentDate)) _thirdRow += "<td id='GC_" + counter++ + "' class='cw-gantt-today' style='height:" + this.tasks.length * 21 + "px' >&nbsp;</td>";
              else _thirdRow += "<td id='GC_" + counter++ + "' class='cw-gantt-weekend' style='height:" + this.tasks.length * 21 + "px ' >&nbsp;</td>";
            } else {
              _gStr += "<td class='cw-gantt-day'><div style='width:24px;'>" + _dTemp.getDate() + '</div></td>';
              if (Date.parse(_dTemp) == Date.parse(_currentDate)) _thirdRow += "<td id='GC_" + counter++ + "' class='cw-gantt-today' style='height:" + this.tasks.length * 21 + "px'>&nbsp;</td>";
              else _thirdRow += "<td id='GC_" + counter++ + "' class='cw-gantt-day'>&nbsp;</td>";
            }
            if (_dTemp.getDate() < util.getDaysInMonth(_dTemp.getMonth() + 1, _dTemp.getFullYear())) {
              if (Date.parse(_dTemp) == Date.parse(_maxDate)) {
                _firstRowStr += "<td class='cw-gantt-month' colspan='" + (_colSpan + 1) + "'>" + _dTemp.getFullYear() + '-' + (_dTemp.getMonth() + 1) + '</td>';
              }
              _dTemp.setDate(_dTemp.getDate() + 1);
              _colSpan++;
            } else {
              _firstRowStr += "<td class='cw-gantt-month' colspan='" + (_colSpan + 1) + "'>" + _dTemp.getFullYear() + '-' + (_dTemp.getMonth() + 1) + '</td>';
              _colSpan = 0;
              if (_dTemp.getMonth() == 11) {
                //December
                _dTemp.setFullYear(_dTemp.getFullYear() + 1, 0, 1);
              } else {
                _dTemp.setFullYear(_dTemp.getFullYear(), _dTemp.getMonth() + 1, 1);
              }
            }
          }
          _thirdRow += '</tr>';

          _gStr += '</tr>' + _thirdRow;
          _gStr += '</table>';
          _gStr = _firstRowStr + _gStr;

          for (let i = 0; i < this.tasks.length; i++) {
            _offSet = (Date.parse(this.tasks[i].getFrom()) - Date.parse(_minDate)) / (24 * 60 * 60 * 1000);
            _dateDiff = (Date.parse(this.tasks[i].getTo()) - Date.parse(this.tasks[i].getFrom())) / (24 * 60 * 60 * 1000) + 1;
            _gStr += "<div style='position:absolute; top:" + (offsetY + 20 * (i + 2)) + 'px; left:' + (offsetX + _offSet * 27 + 206) + 'px; width:' + (27 * _dateDiff - 1 + 100) + "px;'><div title='" + this.tasks[i].getTask() + "' class='cw-gantt-task' style='float:left; width:" + (27 * _dateDiff - 1) + "px;'>" + util.getProgressView(this.tasks[i].getProgress()) + "</div><div class='cw-gantt-task-resource' style='float:left; padding-left:2px'>" + this.tasks[i].getResource() + '</div></div>';
            _gStr += "<div class='cw-gantt-task-text' style='position:absolute; top:" + (offsetY + 20 * (i + 2) + 1) + 'px; left:' + (offsetX + 0) + "px'>" + this.tasks[i].getTask() + '</div>';
          }

          this.ganttView.innerHTML = _gStr;
        }
      }
    };

    return gantt;
  },

  /* --------- SICON GANTT CHART -----------------------------------------------------------
  * AUTHOR : Dathq - ICT Service Engineering Jsc, - dathq@ise.com.vn
  * LICENSE : Free
  * DESCRIPTION : Create a new task item with these info
  * - from: start date (format: mm/dd/dddd)
  * - to: deadline of task (format: mm/dd/dddd)
  * - task: name of the task, what has to be solved (not includes ')
  * - resource: who have to solve this task (not includes ')
  * - progress: how is it going? (format: integer value from 0 to 100, not includes %)
  *----------------------------------------------------------------------------------------*/
  newTask: function(from, to, task, resource, progress) {
    var taskObject = {
      _from: new Date(),
      _to: new Date(),
      _task: task,
      _resource: resource,
      _progress: progress,

      getFrom: function() {
        return this._from;
      },
      getTo: function() {
        return this._to;
      },
      getTask: function() {
        return this._task;
      },
      getResource: function() {
        return this._resource;
      },
      getProgress: function() {
        return this._progress;
      },

      create: function(from, to, task, resource, progress) {
        var dateValues = from.split('-');
        this._from.setFullYear(parseInt(dateValues[0], 10), parseInt(dateValues[1], 10) - 1, parseInt(dateValues[2], 10));
        dateValues = to.split('-');
        this._to.setFullYear(parseInt(dateValues[0], 10), parseInt(dateValues[1], 10) - 1, parseInt(dateValues[2], 10));
      }
    };

    taskObject.create(from, to, task, resource, progress);

    return taskObject;
  }
};

export default gantt;

/*----- END OF MY CODE FOR Gantt CHART GENERATOR -----*/



